import { createStore } from "vuex";
import axios from "axios";

// Configure Axios with the base URL for the backend
const apiClient = axios.create({
  baseURL: "http://192.168.1.10:8080", // Replace with your actual backend URL
  headers: {
    "Accept": "application/json",               
  },
});

// Automatically attach token for authenticated requests
apiClient.interceptors.request.use((config) => {
  const token = localStorage.getItem("token");
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

const store = createStore({
  state: {
    user: {
      email: "",
    },
    isAuthenticated: false,
    token: null,
  },
  mutations: {
    setUser(state, user) {
      state.user.email = user.email;
      state.token = user.token || null;
      state.isAuthenticated = !!user.token;
      if (user.token) {
        localStorage.setItem("token", user.token);
      }
    },
    logoutUser(state) {
      state.user = { email: "" };
      state.isAuthenticated = false;
      state.token = null;
      localStorage.removeItem("token");
    },
  },
  actions: {
    async loginUser({ commit }, { email, password }) {
      try {
        const formData = new FormData();
        formData.append("email", email);
        formData.append("password", password);

        const response = await apiClient.post("/admin/AdminLogin", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        });

        if (response.data && response.data.token) {
          commit("setUser", { 
            email: email, 
            token: response.data.token 
          });
          return response.data;
        } else {
          throw new Error("Invalid response from server");
        }
      } catch (error) {
        console.error("Error during login:", error);
        throw error;
      }
    },
    logoutUser({ commit }) {
      commit("logoutUser");
    },
  },
  getters: {
    getUserEmail: (state) => state.user.email,
    isAuthenticated: (state) => state.isAuthenticated,
    getAuthToken: (state) => state.token,
  },
});

export default store;